import {ModuleWithProviders, NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';

import {AdaptRxFormControlModule} from '../form-control/index';
import {AdaptRxUploaderComponent} from './adapt-rx-uploader.component';
import {AdaptUploadItemModule} from '../../uploader-item/index';
import {AdaptUploaderModuleConfig} from '../../common/uploader/models/uploader.models';
import {AdaptFileSizePipeModule} from '../../common/uploader/pipes/file-size.pipe';
import {AdaptFileTypesPipeModule} from '../../common/uploader/pipes/file-type.pipe';
import {SendFileService} from '../../common/uploader/services/send.file.service';
import {AdaptTooltipModule} from '../../tooltip/index';
import {AdaptButtonModule} from '../../button/index';
import {AdaptAlertModule} from '../../alert/index';
import {AdaptPopoverModule} from '../../popover/index';
import {AdaptCollapseModule} from '../../collapse/index';
import {AdaptEmptyStateModule} from '../../empty-state/index';

@NgModule({
  imports: [
    CommonModule,
    AdaptRxFormControlModule,
    AdaptButtonModule,
    AdaptTooltipModule,
    AdaptUploadItemModule,
    AdaptFileSizePipeModule,
    AdaptFileTypesPipeModule,
    AdaptAlertModule,
    AdaptPopoverModule,
    AdaptCollapseModule,
    AdaptEmptyStateModule
  ],
  declarations: [
    AdaptRxUploaderComponent
  ],
  exports: [
    AdaptRxUploaderComponent
  ]
})
export class AdaptRxUploaderModule {
  static forRoot(config?: AdaptUploaderModuleConfig): ModuleWithProviders<AdaptRxUploaderModule> {
    return {
      ngModule: AdaptRxUploaderModule,
      providers: [
        // TODO: investigate how service logic should work and rewrite -
        //  what exactly confusing: url (for post and delete methods) and/or service in the module?
        (config && config.uploader) ? config.uploader : {provide: 'UploaderService', useClass: SendFileService},
        {provide: SendFileService, useClass: SendFileService}
      ]
    };
  }
}

export * from './adapt-rx-uploader.component';
export * from './adapt-rx-uploader-config.service';
